import navBar from '@/components/nav-bar.vue';
import dividLine from '@/components/line.vue';
import {
	newPhoneyzm,
	newPhone
} from '@/api/user'
export default {
	data() {
		return {
			phone: '',
			pphone: '',
			code: '',
			isSend: false,
			yzm: '发送验证码',
			newphone: '',
			isShow: false,
		}
	},
	components: {
		navBar,
		dividLine
	},
	onLoad() {
		this._getUser()
	},
	methods: {
		back(){
			uni.navigateBack({
				delta:1
			})
		},
		clear() {
			this.newphone = ''
		},
		_getUser() {
			try {
				const user = uni.getStorageSync('user');
				this.phone = user.phone
				let start = this.phone.substring(0, 3);
				let last = this.phone.substring(8, 12);
				this.pphone = `${start}*****${last}`
				console.log('user', this.phone)
			} catch (e) {
				console.log('ERROR', e.message)
			}
		},
		checkphone(e) {
			this.newphone = e.detail.value;
			if (!this.newphone) {
				this._showToast('手机号码不能为空', 'none')
				this.isabled = true
				this.isSend = true
				return
			} else if (!/^[1][3,4,5,7,8][0-9]{9}$/.test(this.newphone)) {
				this._showToast("请输入正确的手机号码格式", "none")
				this.isabled = true
				this.isSend = true
				return
			}
			this.isabled = false
			this.isSend = false
		},
		send() {
			if (!this.isSend && this.phone) {
				newPhoneyzm(this.newphone).then(res => {
					if (res.code == 100) {
						uni.showToast({
							title: res.msg,
							icon: "none"
						})
						return
					}
					uni.showToast({
						title: '验证码已发送'
					})
					let timer = null;
					let cut = 60;
					this.isSend = true;
					timer = setInterval(() => {
						this.yzm = --cut + "S"
						if (cut == 0) {
							clearInterval(timer)
							this.yzm = '发送验证码'
							this.isSend = false
						}
					}, 1000)
				})
			}
		},
		submit() {
			if (!this.isSend) {
				newPhone(this.newphone, this.code).then(res => {
					if (res.code == 200) {
						uni.showToast({
							title: '修改成功'
						})
						try {
							const user = uni.getStorageSync('user');
							user.phone = this.newphone;
							uni.setStorageSync('user', user);
						} catch (e) {

						}
						setTimeout(() => {
							uni.reLaunch({
								url: '/pages/index/setting'
							})
						}, 500)
					}
				})
			}
		}
	},
	watch: {
		newphone() {
			if (!this.newphone) {
				this.isShow = false
			} else {
				this.isShow = true
			}
		}
	}
}
